<template>
  <div class="view">
    <div class="list" v-if="isValidLogin">
      <div class="item" v-for="item in userList" :key="item.id" @click="switchTalk(item)">
        <img class="item-avatar" :src="item.avatar" :alt="item.nickName" />
        <div>
          <div class="item-name">{{ item.nickname }}</div>
          <div class="item-msg" v-text="getLastMsg(item)"></div>
        </div>
      </div>
    </div>
    <div v-else class="login-tip" @click="$store.dispatch('user/showLoginPop')">
      <span>登录查看消息</span>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {};
  },
  computed: {
    ...mapState({
      userList: state => state.chat.userList,
      msgList: state => state.chat.msgList,
      msgGroup: state => state.chat.msgGroup,
      userInfo: state => state.user.userInfo,
      isValidLogin: state => state.user.isValidLogin
    })
  },
  methods: {
    getLastMsg(user) {
      return this.msgGroup[user.id][0]['content'];
    },
    switchTalk(user) {
      this.$store.dispatch('chat/switchTalkPage', user);
    }
  }
};
</script>

<style lang="scss" scoped>
.view {
  background-color: #eee;
}

.item {
  display: flex;
  overflow: hidden;
  font-size: 12px;
  padding: 10px 10px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
}

.item-avatar {
  float: left;
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 50%;
}

.item-msg {
  color: #666;
}

.item-name {
  font-size: 14px;
  margin-bottom: 4px;
}

.login-tip {
  padding: 20px;
  padding-top: 40%;
  text-align: center;
  color: #999;
}
</style>



